import { Fragment } from 'react'
import PropTypes from 'prop-types'
import NavDrawer, {
  DrawerHeader,
  DrawerSubtitle,
  DrawerTitle,
  DrawerContent,
} from '@material/react-drawer'
import DrawerList from './DrawerList'

const Contents = ({ setIsDrawerOpen }) => (
  <Fragment>
    <DrawerHeader>
      <DrawerTitle>
        Drawer Title
      </DrawerTitle>
      <DrawerSubtitle>
        Drawer Subtitle
      </DrawerSubtitle>
    </DrawerHeader>
    <DrawerContent>
      <DrawerList
        onItemClick={() => setIsDrawerOpen(false)}
      />
    </DrawerContent>
  </Fragment>
)
Contents.propTypes = {
  setIsDrawerOpen: PropTypes.func.isRequired,
}

const Drawer = ({ breakpoint, isOpen, setIsDrawerOpen }) => (
  <Fragment>
    <NavDrawer
      className='mobile-only'
      modal={true}
      open={isOpen}
      onOpen={() => setIsDrawerOpen(true)}
      onClose={() => setIsDrawerOpen(false)}
    >
      <Contents
        setIsDrawerOpen={setIsDrawerOpen}
      />
    </NavDrawer>
    <NavDrawer
      className='non-mobile-only'
    >
      <Contents
        setIsDrawerOpen={setIsDrawerOpen}
      />
    </NavDrawer>
  </Fragment>
)
Drawer.propTypes = {
  breakpoint: PropTypes.number.isRequired,
  isOpen: PropTypes.bool.isRequired,
  setIsDrawerOpen: PropTypes.func.isRequired,
}

export default Drawer
